import { Block, BlockChildren } from '@ali/4ever-cangjie';
import type { MoInterfaces } from '@ali/4ever-cangjie';
import { ELEMENT_TYPE_TABLE_CELL } from '../../utils/meta';

interface TableCellData {
  readonly width?: number;
  readonly colSpan?: number;
  readonly rowSpan?: number;
  bdr?: MoInterfaces.TableBorder.default;
  fill?: string;
  readonly color?: string;
  readonly border?: string;
  readonly vAlign?: string;
  readonly hidden?: boolean;
  uuid?: string;
  cellMar?: Record<string, any>;
  shd?: {
    fill?: string;
  };
}

type TableCellChildren = Block[];

interface TableCellProperties<D = TableCellData, N extends BlockChildren = TableCellChildren> {
  data?: D;
  nodes?: N;
}

export default class TableCell extends Block<TableCellData, TableCellChildren> {
  static TYPE = ELEMENT_TYPE_TABLE_CELL;

  static create<SD = TableCellData, SN extends BlockChildren = TableCellChildren>(
    properties: TableCell | TableCellProperties<SD, SN> | string,
  ) {
    if (typeof properties === 'string') {
      throw new Error('`TableCell.create` does not accept string');
    }
    if (TableCell.isTableCell(properties)) {
      return properties as Block<SD, SN>;
    }
    return super.create<SD, SN>({
      ...properties,
      type: TableCell.TYPE,
    });
  }

  static isTableCell(maybeTableCell: any): maybeTableCell is TableCell {
    return Block.isBlock(maybeTableCell) && maybeTableCell.type === TableCell.TYPE;
  }

  static isValidTableCell(maybeTableCell: any): maybeTableCell is TableCell {
    return this.isTableCell(maybeTableCell)
      && maybeTableCell.nodes.length >= 1
      && Block.isBlock(maybeTableCell.nodes[0]);
  }
}
